<template>
  <div class="pay">
    <van-nav-bar title="确认订单" left-text="返回" left-arrow @click-left="$router.go(-1)" />

    <div class="choose">
      <div class="address">
        <van-cell is-link @click="showPopup">
          <div class="div">
            <span class="span1">{{useAddressInfo.address}}</span>
          </div>
        </van-cell>
        <van-popup v-model="show" position="bottom" round class="content">
          <van-address-list
            v-model="aid"
            :list="addresslist"
            default-tag-text="默认"
            @select="selectItem"
            @add="onAdd"
          />
        </van-popup>
      </div>
    </div>

    <div class="order clearfix">
      <h3>
        商品数量
        <span class="fr">共{{count}}件</span>
      </h3>
      <div class="item clearfix" v-for="(item,index) in orderData" :key="index">
        <div class="img fl">
          <img :src="item.small_img" alt class="img_auto" />
        </div>
        <div class="text fl">
          <div class="name">
            <span style="margin-right:15px">{{item.name}}</span>
            <br />
            <span>{{item.rule}}</span>
          </div>
          <div class="bottom">
            <div class="price fl">
              ￥{{item.price}}
              <span style="margin:0 15px">x</span>
              {{item.count}}
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="goods">
      <h3>
        商品价格
        <span class="fr span1">￥{{total/100}}.00</span>
      </h3>
      <h3>
        支付方式
        <span class="fr span2">￥{{total/100}}.00</span>
      </h3>
    </div>

    <div class="pay-ways">
      <van-radio-group v-model="radio">
        <van-cell-group>
          <van-cell title="支付宝支付" clickable @click="radio = '1'" :icon="img.src1">
            <template #right-icon>
              <van-radio name="1" />
            </template>
          </van-cell>
          <van-cell title="微信支付" clickable @click="radio = '2'" :icon="img.src2">
            <template #right-icon>
              <van-radio name="2" />
            </template>
          </van-cell>
        </van-cell-group>
      </van-radio-group>
    </div>
    <!-- <div class="discount">
      优惠券单元格
      <van-coupon-cell :coupons="coupons" :chosen-coupon="chosenCoupon" @click="showList = true" />
      优惠券列表
      <van-popup v-model="showList" round position="bottom" style="height: 90%; padding-top: 4px;">
        <van-coupon-list
          :coupons="coupons"
          :chosen-coupon="chosenCoupon"
          :disabled-coupons="disabledCoupons"
          @change.once="onChange"
          @exchange="onExchange"
        />
      </van-popup>
    </div>-->

    <div class="time">
      <van-cell is-link @click="showPopupOn">
        <div class="words">
          <p>
            预计送达时间
            <span class="fr" v-if="isable">{{estimateTime}}</span>
          </p>
        
        </div>
      </van-cell>
 
    </div>

    <van-submit-bar :price="total" button-text="提交订单" @submit="onSubmit" />
  </div>
</template>

<script>
const coupon = {
  available: 1,
  condition: "无使用门槛\n最多优惠15元",
  reason: "",
  value: 1500,
  name: "优惠券名称",
  startAt: 1489104000,
  endAt: 1514592000,
  valueDesc: "15",
  unitDesc: "元"
};
export default {
  data() {
    return {
      sids: "",
      show: false,
      on: false,
      value: "",
      addresslist: [],
      useAddressInfo: {
        address: "请选择收货地址",
        phone: "",
        receiver: ""
      },
      aid: "",
      orderData: "",
      total: 0,
      count: 0,
      radio: "1",
      currentTime: "24:00",
      time: {
        hour: 0,
        minute: 59
      },
      arriverTime: "",
      chosenCoupon: -1,
      coupons: [coupon],
      disabledCoupons: [coupon],
      showList: false,
      img: {
        src1: require("../assets/images/pay.jpg"),
        src2: require("../assets/images/weixin.jpg")
      },
      estimateTime: "",
      isable: true
    };
  },
  created() {
    this.sids = this.$route.query.sids.split("-");
    
    this.getAddress();
    this.getOrderData(this.sids);
    let now = new Date();
    let hour = now.getHours();
    let minutes = now.getMinutes();
    
    
    this.time.hour = hour;
    this.time.minute = minutes;
    this.time.minute = minutes + 30;
    let Time = minutes + 30;
    if (Time > 60) {
      Time = Time - 60;
      hour = hour + 1;
    }
    this.estimateTime = hour + ":" + Time;
  },
  methods: {
    onAdd() {
      
      this.$router.push({ name: "修改地址" });
    },
    onChange(index) {
      this.showList = false;
      this.chosenCoupon = index;
      
      const total = Number(this.total) - coupon.value;
      this.total = total;
      // coupon.reson="hahaha"
    },
    onExchange() {
      this.coupons.push(coupon);
    },
    showPopup() {
      this.show = true;
    },
    showPopupOn() {
      this.on = true;
    },
    close() {
      this.on = !this.on;
    },
    confirm(value) {
      
      this.arriverTime = value;
      this.on = !this.on;
      this.isable = false;
    },
    //获取地址信息
    getAddress() {
      let tokenString = localStorage.getItem("__jigng");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "get",
        url: "/findAddress",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(res => {
          this.$toast.clear();
          
          if (res.data.code == 20000) {
            res.data.result.forEach(v => {
              let addressobj = {
                id: v.aid,
                tel: v.tel,
                name: v.name,
                address: v.province + v.city + v.county + v.addressDetail,
                //转成布尔值判断是否默认
                isDefault: Boolean(v.isDefault)
              };
              
              
              //默认选中
              if (v.isDefault) {
                //赋值
                this.aid = v.aid;
                
              }
              this.addresslist.push(addressobj);
            });
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    selectItem(item) {
      
      this.useAddressInfo.address = item.address;
      this.useAddressInfo.phone = item.tel;
      this.useAddressInfo.receiver = item.name;
      this.show = !this.show;
    },
    //获取提交订单数据
    getOrderData(sids) {
        if(this.useAddressInfo.address=="请选择收获地址"){
          return
        }


      let tokenString = localStorage.getItem("__jigng");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "get",
        url: "/commitShopcart",
        params: {
          appkey: this.appkey,
          tokenString,
          sids: JSON.stringify(this.sids)
        }
      })
        .then(res => {
          
          if (res.data.code == 50000) {
            res.data.result.forEach(v => {
              this.total += v.price * v.count * 100;
              this.count += v.count;
            });

            this.orderData = res.data.result;
            
          }
        })
        .catch(err => {
          
        });
    },
    onSubmit() {
      let tokenString = localStorage.getItem("__jigng");
      this.$toast.loading({
        message: "支付中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "post",
        url: "/pay",
        data: {
          appkey: this.appkey,
          tokenString,
          sids: JSON.stringify(this.sids),
          phone: this.useAddressInfo.phone,
          address: this.useAddressInfo.address,
          receiver: this.useAddressInfo.receiver
        }
      })
        .then(res => {
          if (res.data.code == 60000) {
            this.$toast("免密支付成功");
            this.$router.push({ name: "订单" });
          }
          
        })
        .catch(err => {
          
        });
    }
  }
};
</script>

<style lang="less" scoped>
.pay {
  padding-bottom: 50px;
  color: black;
  .van-nav-bar {
    border-bottom: 1px solid gray;
  }
  .choose {
    .van-popup--bottom.van-popup--round {
      height: 400px;
    }
    /deep/ .van-radio__icon {
      display: none;
    }
    .van-cell--clickable {
      border-bottom: 3px dashed #135cfe;
      margin-bottom: 15px;
    }
    /deep/ .van-cell__value--alone {
      color: #323233;
      font-size: 18px;
    }
    /deep/ .van-address-list {
      height: 300px;
      padding-bottom: 30px;
    }
    .div {
      height: 25px;
      line-height: 25px;
      .span1 {
        font-size: 14px;
      }
    }
  }

  .order {
    width: 92%;
    margin: 0 auto;
    .item {
      width: 98%;
      margin: 0px auto;
      height: 100px;
      box-shadow: 3px 4px 5px 6px #f2f2f2;
      margin-bottom: 15px;
      .img {
        width: 30%;
        height: 100px;
        line-height: 100px;
        .img_auto {
          border-radius: 10px;
          width: 80%;
          margin: 9px;
        }
      }
      .text {
        width: 60%;
        margin-left: 15px;
        height: 100px;
        padding-top: 15px;
        .name {
          height: 50px;
        }
        p {
          margin: 5px;
          padding: 0;
        }
      }
      .bottom {
        width: 100%;
        height: 30px;
        line-height: 30px;
      }
    }
  }
  .goods {
    width: 85%;
    margin: 0 auto;
    padding: 10px;
    height: 80px;
    line-height: 30px;
    box-shadow: 2px 2px 3px 3px #f2f2f2;
    border-radius: 10px;
  }
  .pay-ways {
    width: 98%;
    margin: 10px auto;
    /deep/ .van-icon__image {
      width: 2em;
      height: 2em;
    }
    /deep/ .van-cell__title {
      margin-top: 3px;
    }
    .span2 {
      color: #ababab;
      font-size: 12px;
    }
  }

  .discount {
    width: 90%;
    margin: 0 auto;
  }
  .time {
    width: 90%;
    margin: 0 auto;
  }
}

/deep/ .van-address-item__edit {
  display: none;
}
/deep/ .van-cell__right-icon {
  display: none;
}
/deep/ .van-submit-bar__text {
  text-align: left;
  color: black;
}
</style>